/*
	Utilities
*/
/*
	Media queries
*/
* {
  box-sizing: border-box; }

html {
  position: relative; }

body {
  margin-bottom: 75px;
  font-family: 'Helvetica Neue Light', 'Helvetica', 'Arial', sans-serif;
  font-smoothing: antialiased;
  background-image: url("img/nodebg.jpg"); }

#content {
  display: flex;
  flex-direction: row;
  flex-flow: center;
  margin: 20px auto;
  padding: 10px; }

#main-column {
  width: 100%; }

/*
	Components
*/
nav-dir {
  flex-shrink: 0;
  min-width: 150px;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  color: #fff;
  font-size: 1rem;
  margin-top: 20px;
  background: #242425;
  border-radius: 7px 0 0 7px;
  border: solid 1px #000;
  padding: 5px; }
  nav-dir h1 {
    margin: 0.5em 0.1em;
    color: #fff;
    font-size: 1.5em; }

nav-dir ul {
  padding-left: 10px;
  margin: 0; }

nav-dir span, nav-dir li {
  padding-left: 5px;
  display: block;
  cursor: pointer; }

nav-dir span:hover, nav-dir li:hover {
  background-color: rgba(0, 0, 0, 0.5); }

nav-dir li {
  padding-left: 10px; }

nav-dir li.selected {
  background-color: #999;
  color: #000; }

content-header {
  display: block;
  border-radius: 8px 8px 0 0;
  background: #eaeaea;
  background: linear-gradient(top, #eaeaea 0%, #dddddd 100%);
  border-bottom: 1px solid #cbcbcb;
  padding: 20px;
  color: #484848;
  text-shadow: 0px 1px 0px #ffffff; }
  content-header h2 {
    margin: 0; }

file-view {
  display: block;
  min-height: 500px;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  padding: 20px;
  background-color: #fff; }
  file-view p {
    margin: 10px 0; }
  file-view a {
    color: #1563f1; }
  file-view a.link-missing {
    color: #F18315; }

#drop-zone {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 200px;
  background-color: #eee;
  padding: 20px; }

#drop-zone.dropping {
  border: 2px dashed #3381d4; }

